import {Component, Inject, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AuthFacade, NotificationService} from 'af-services';
import {Router} from '@angular/router';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})
export class LoginFormComponent implements OnInit {

  form: FormGroup;
  captchaSuccess = false;
  show = false;
  error;


  callingControl: any;
  captchaError = false;
  constructor(private fb: FormBuilder, private authFacade: AuthFacade, private notify: NotificationService, @Inject('captchaKey') public captchaKey, @Inject('afterLoginRedirectTo') private afterLoginRedirectTo, private router: Router) { }

  async ngOnInit() {

    this.form = this.fb.group(
      {

        email: ['', Validators.compose([Validators.required, Validators.email])],
        password: ['', Validators.compose([Validators.required, Validators.minLength(6)])]
      }
    );

    await this.authFacade.readAndLoginWithToken('btnGoogle', (result) => {
      this.onSuccessfulLogin(result);

    }, e => {
    });

  }

  async submit(callingControl) {
    this.callingControl = callingControl;
    if (!this.error) {
      this.doLogin(callingControl);
    } else if (this.captchaSuccess) {
      this.doLogin(callingControl);
    }
  }

  showHidePassword() {
    this.show = !this.show;
  }

  handleSuccessCaptcha($event: string) {
    this.form.patchValue({
      captcha: $event
    });
    this.captchaSuccess = true;

  }

  private doLogin(callingControl) {
    this.captchaSuccess = false;
    this.error = undefined;

    this.authFacade.loginWIthUsernameAndPassword(this.form.value, callingControl, (result) => {
      this.onSuccessfulLogin(result);

    }, e => {
      this.error = e;
    });
  }

  private onSuccessfulLogin(result) {
    const redirectTo = localStorage.getItem('redirectTo');
    if (redirectTo) {
      this.notify.info('Redirecting...', 'Please Wait', 10000, true, true);
      localStorage.removeItem('redirectTo');
      window.location.href = redirectTo;
      return;
    }
    if (result.isMTurk) {
      this.router.navigate([this.afterLoginRedirectTo]);

      return;
    }
    if (window.opener) {

      window.close();
    } else {
      this.router.navigate([this.afterLoginRedirectTo]);
    }
  }

}
